<template>
  <div class="payment">
    <div class="payment_top">
      <topBar></topBar>
    </div>
    <div class="payment_center">
      <p class="payment_bill">账单详情</p>
      <!--<a href="javascript:void(0)" class="back_btn" @click="back">返回</a>-->
      <div class="payment_table">
        <table>
          <tr style="color: #9ea1b2;">
            <th style="width: 14%">商品名称</th>
            <th style="width: 50%;">有效日期</th>
            <th>价格</th>
            <th>折扣</th>
            <th>金额</th>
          </tr>
          <tr class="td" v-for="(item,key) in data">
            <td>{{ item.name }}</td>
            <td>{{ item.start_time }} ~{{ item.over_time }}</td>
            <td>￥{{ item.price }}</td>
            <td style="color: #9ea1b2;">无折扣</td>
            <td>￥{{ item.all_money }}</td>
          </tr>
        </table>
        <!--<p v-if="data.length===0"  class="td_data">
         {{msg}}
        </p>-->
      </div>
      <div class="payment_relation">
        <div class="payment_hint">
          <p>有疑问？联系我们吧</p>
          <p>如果您已成功支付，请刷新此页面，不需要重复支付</p>
        </div>
        <div class="payment_fu">
          <p><span>应付金额</span><span style="color: #a5b73d;">￥{{ all_money }}</span></p>
          <p v-show ="areShow" class="pay_btn">
            <span @click="lastPay">用余额支付</span>
            <span @click="wxPay">用微信支付</span>
            <span>用支付宝支付</span></p>
          <p v-show = "!areShow">已支付</p>
        </div>
      </div>
    </div>
    <Modal
      v-model="modal"
      :title="title"
      @on-ok="ok"
      @on-cancel="cancel">
      <p style="text-align: center"><img :src="pic" alt="" style="width: 150px;height: 150px"></p>
      <p style="text-align: center;margin: 0.2rem 0">
        <span style="display:inline-block;width: 150px;background-color: #445e84">
          <img src="../../assets/tips-rwm.png" alt="">
        </span>
      </p>
      <p style="text-align: center"><img src="../../assets/sweep-text.jpg" alt=""></p>
    </Modal>
  </div>
</template>

<script>
  import topBar from './topBar.vue'
  export default {
    data() {
      return {
        modal:false,
        title:"微信支付",
        msg: "正在加载....",
        data: [],
        areShow:true,
        appid:"",
        pic:"",
        time:"",
        all_money:''
      }
    },
    methods:{
      lastPay(){
          this.$Modal.confirm({
            title: '余额支付',
            content: '<p>确定使用余额支付?</p>',
            onOk: () => {
              this.post("custom/Own/buyUserApp",{
                appid: this.appid
              }).then( res => {
                if (res.data.code === 10000){
                  this.$Modal.success({
                    title: "购买成功",
                    content: res.data.msg
                  });
                } else {
                  this.$Modal.error({
                    title: "购买失败",
                    content: res.data.msg
                  });
                  }
              })
            },
            onCancel: () => {

            }
          });
        },
      ok(){
        location.reload();
      },
      cancel(){

      },
      back(){
          this.$router.go(-1);
      },
      wxPay(){
          this.title = "微信支付";
          this.post("custom/Recharge/wxBuyApp",{
              appid: this.appid
          }).then( res => {
             if (res.data.code === 10000){
               this.pic = res.data.data;
               this.modal = true;
             } else {
               this.$Modal.error({
                 title: "获取支付码失败",
                 content: res.data.msg
               });
             }
          })
      },
      getAppMeal (appid) {
        this.post("custom/Price/get_app_detail ",{
          appid
        }).then(res => {
          if (res.data.code === 10000) {
              this.all_money = res.data.data.all_money;
              let arr = [];
              arr.push(res.data.data);
              this.data = arr;
          }
        })
      }
    },
    components: {
      topBar
    },
    created(){
      if(this.$route.name==="payment"){
        this.$store.dispatch("fetch_load" ,false)
      }
      this.appid = this.$route.params.appid;
      this.getAppMeal(this.appid);
    }
  }
</script>

<style scoped>
  .back_btn{
    display: inline-block;
    padding: 0.1rem 0.3rem;
    color: black;
    background: #f9f9f9;
    border: 1px solid #eeeeee;
    border-radius: 0.05rem;
  }
  .payment_center {
    width: 12rem;
    margin: 0 auto;
  }

  .payment_table {
    margin-bottom: 0.2rem;
  }

  .payment_bill {
    margin: 0.1rem 0 0.1rem 0;
    font-size: 0.18rem;
    text-align: center;
    color: #57606F;
  }

  .payment_relation {
    display: flex;
    justify-content: space-between;
  }

  .payment_hint>p:nth-child(1) {
    font-size: 0.12rem;
    color: #57afe1;
    margin: 0.2rem 0 0.2rem 0;
  }

  .payment_hint>p:nth-child(2) {
    font-size: 0.12rem;
    color: black;
  }

  .payment_fu>p:nth-child(1) {
    margin: 0.1rem 0 0.1rem 0;
  }

  .payment_fu>p:nth-child(1)>span {
    font-size: 0.18rem;
  }

  .payment_fu>p:nth-child(2)>span {
    font-size: 0.16rem;
    display: inline-block;
    width: 1.2rem;
    height: 0.4rem;
    line-height: 0.4rem;
    border-radius: 0.04rem;
    text-align: center;
    color: #fffbff;
    margin-right: 0.2rem;
    cursor: pointer;
  }

  .payment_fu>p:nth-child(2)>span:nth-child(1) {
    background: #aa7171;
  }

  .payment_fu>p:nth-child(2)>span:nth-child(2) {
    background: #9bb43e;
  }

  .payment_fu>p:nth-child(2)>span:nth-child(3) {
    background: #60aea1;
  }

  table {
    width: 100%;
    font-size: 0.14rem
  }

  th {
    padding: 0.1rem;
    text-align: left
  }
  table>tr{

    border-bottom: 2px solid #bbbcc0;
  }
  .td{
    text-align: left;
  }
  .td>td {
     font-size: 0.18rem;
    padding: 0.2rem 0.1rem;
    color: #000000;
    font-size: 0.14rem;
    border-bottom: 1px solid #f2f2f2;
  }

  .info {
    color: #576b95;
  }

  td {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .td_data {
    width: 100%;
    padding: 0.2rem 0.1rem;
    text-align: center;
    border-bottom: 1px solid #f2f2f2;
  }
  .pay_btn>span:hover{
      opacity: 0.5;
  }
</style>



